
.conicrose-image-gallery {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 8px;
  width: 100%;
}

.conicrose-image-gallery img {
  width: 100%;
  max-width: 100%; /* Ensure image doesn't stretch too large */
  height: auto; /* Maintain aspect ratio */
  object-fit: cover;
}


@media screen and (max-width: 768px) {
  .edited-image-gallery img {
    max-width: 80px;
  }
}